<template>
  <section class="white-section dropper-section">
    <b-container>
      <b-row>
        <h2>Чем Вам поможет капельница</h2>
        <b-col md="8">
          <ul class="dropper-section_list">
            <li v-for="(item, key) in list" :key="key">
              <h5>{{ item.title }}</h5>
              <p>{{ item.text }}</p>
            </li>
          </ul>
        </b-col>
        <b-col lg="3" md="4" offset-lg="1">
          <div class="time-block_wrap">
            <h3><span>Время работы</span></h3>
            <div style="color: #dc3545;" class="time-text">Круглосуточно</div>
            <div style="margin: 10px 0;" class="time-text">Без праздников</div>
            <div class="time-text">Без выходных</div>
            <div class="time-text-usl">
              C 00:00 - 07:00 цена услуг оговаривается индивидуально
            </div>
            <b-button v-call size="lg">
              Позвонить
            </b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>
  export default {
    name: 'dropper-section',
    data: () => ({
      list: [
        {
          title: '1. Симптоматическая терапия',
          text: 'Медикаментозным путем убираем все симптомы интоксикации (тошнота, рвота, головная боль, головокружение, слабость, дрожь в теле)'
        },
        {
          title: '2. Дезинтоксикационная терапия',
          text: 'Солевые и дезинтоксикационные растворы помогут Вашему организму избавиться от токсинов, восстановят водно-солевой балланс организма, что приведет к нормализации его состояния.'
        },
        {
          title: '3. Снотворные препараты',
          text: 'Современные снотворные препараты, помогут нормализовать сон пациента, что скорее приведет к его моральному и физическому благополучию.'
        }
      ]
    })
  }
</script>

<style scoped lang="scss">
  @import "../../static/scss/variables";

  .dropper-section {
    .dropper-section_list {
      text-align: center;
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      li {
        h5 {
          font-size: 1.5em;
          color: $primary_color;
          font-weight: bold;
        }
        p {
          font-size: 1.5em;
          margin: 0;
        }
      }
    }
    .time-block_wrap {
      text-align: center;
      border: 2px solid $accent_color;
      border-radius: 0.25rem;
      .time-text {
        font-size: 1.5em;
        color: #615f5f;
        font-family: "DAYS"
      }
      .time-text-usl {
        font-size: 0.8em;
        color: #615f5f;
        margin-top: 30px;
        margin-bottom: 20px;
      }
      h3 {
        padding: 15px;
        font-family: "Days";
        color: white;
        margin-bottom: 30px;
        background-color: $accent_color;
       // background-image: linear-gradient(291deg, $primary_color 13%, $accent_color 11%, $primary_color);
      }
      button {
        margin-bottom: 20px;
        background-color: $primary_color;
      }
    }
  }

  @media (max-width: 992px) {
    .dropper-section {
      .dropper-section_list {
        li {
          margin-bottom: 20px;
        }
      }
    }
  }
</style>



