<template>
  <b-modal ref="callmodal" v-model="$store.state.modal_call_open" hide-footer title="Позвонить">
    <div class="modal-text">Нажмите на телефон</div>
    <div class="modal-text">
      <a :href="`tel:${$store.state.phone}`">
        <b-button size="lg" variant="success">+380 (67) 103-8-103</b-button>
      </a>
    </div>
    <div class="modal-text">Или наведите камеру телефона на QR-code</div>
    <div class="modal-text">
      <img src="../../static/img/qr-code.gif">
    </div>
  </b-modal>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss" scoped>
  h2 {
    font-size: 2em !important;
    margin: 10px !important;
  }
  .modal-text {
    font-size: 1.3em;
    text-align: center;
    button {
      font-size: 1.8em;
      line-height: 1.5em;
      margin: 20px;
    }
  }
</style>



