<template>
  <div class="banner">
    <!--<vue-typed-js :showCursor="false" :typeSpeed="30" :strings="[first_string]">-->
    <!--<div class="banner-spasing">-->
    <!--<h2 class="typing"></h2>-->
    <!--</div>-->
    <!--</vue-typed-js>-->
    <!--<vue-typed-js :showCursor="false" :typeSpeed="30" :startDelay="3200" :strings="['Полная анонимность. ^500 Прибудем за час']">-->
    <!--<h2 class="typing second"></h2>-->
    <!--</vue-typed-js>-->
    <div class="banner-wrap">
      <div class="container">
        <div class="banner-spasing">
          <h2 class="typing">Cнятие алкогольной интоксикации, похмелья и абстиненции</h2>
        </div>
        <h2 class="typing">Помощь на дому и в стационаре</h2>
        <div class="banner-phone">
          <!--<span v-call>+380 (67) 103-8-103</span>-->
          <b-button v-call size="lg" variant="warning">
            Позвонить
          </b-button>
        </div>
      </div>
    </div>
    <!--<div class="banner_mask"></div>-->
  </div>
</template>

<script>
  export default {
    data: () => ({
      first_string: 'Любой район Одессы. ^500     Любое время суток'
    })
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";
  .banner {
    position: relative;
    margin-top: $header_height;
    height: calc(100vh - 120px);
    background-image: url("../../static/img/banner.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    .banner-wrap {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 900px;
      width: 100%;
      z-index: 1;
      background-color: rgba(0,0,0,0.7);
      padding: 40px;
    }
    .banner_mask {
      background: #4e4825c2;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    h2 {
      width: inherit;
      margin: 0 !important;
      color: white;
      font-size: 3.5em;
      font-family: 'Open Sans', sans-serif !important;
      &.second {
        margin-left: 120px !important;
      }
    }
    .banner-spasing {
      padding-bottom: 40px;
    }
    .banner-phone {
      text-align: center;
      padding-top: 40px;
      button {
        font-size: 2em;
        cursor: pointer;
      }
    }
  }

  @media (max-width: 1199px) {
    .banner {
      h2 {
        font-size: 3.5em !important;
      }
    }
  }

  @media (max-width: 992px) {
    .banner {
      h2 {
        font-size: 2em !important;
      }
    }
  }

  @media (max-width: 768px) {
    .banner {
      margin-top: 50px;
      height: calc(100vh - 50px);
    }
  }

  @media (max-width: 544px) {
    .banner {
      h2 {
        font-size: 2em !important;
        &.second {
          margin: 0 !important;
        }
      }
      .banner-phone span {
        font-size: 2em;
      }
    }
  }
</style>



