<template>
  <b-modal ref="callmodal" v-model="$store.state.modal_menu_open" hide-footer title="Меню">
    <div class="modal-menu" @click="go('#uslugi')">Наши услуги</div>
    <div class="modal-menu" @click="go('#onas')">О нас</div>
    <div class="modal-menu" @click="go('#time')">Время работы</div>
    <div class="modal-menu" @click="go('#contacts')">Контакты</div>
  </b-modal>
</template>

<script>
  export default {
    data: () => ({}),
    methods: {
      go (to) {
        this.$store.commit('TOGGLE_MODAL_MENU')
        this.$scroll.to(to)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .modal-menu {
    text-align: center;
    padding: 10px 0px;
    font-size: 1.3em;
    border-bottom: 1px solid #eee;
  }
</style>



