import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let ENV
if (process.env.NODE_DEPLOY_DEV === 'true') {
  ENV = 'dev_production'
} else if (process.env.NODE_ENV === 'production') {
  ENV = 'production'
} else {
  ENV = 'development'
}

const CreateStore = function () {
  const store = new Vuex.Store({
    state: {
      phone: '+380671038103',
      screen_width: 0,
      screen_height: 0,
      menu_open_mobile: false,
      modal_call_open: false,
      modal_menu_open: false,
      env: ENV
    },
    actions: {},
    mutations: {
      SET_DEVICE_WIDTH: (state, { data, height }) => {
        const object = {
          width: data,
          height: height,
          device: data >= 1200 ? 5
            : data >= 992 ? 4
              : data >= 768 ? 3 : data >= 544 ? 2 : 1
        }
        Vue.set(state, 'screen_width', object)
      },
      OPEN_MOBILE_MENU: (state) => {
        Vue.set(state, 'menu_open_mobile', true)
      },
      CLOSE_MOBILE_MENU: (state) => {
        Vue.set(state, 'menu_open_mobile', false)
      },
      TOGGLE_MODAL_CALL: (state) => {
        Vue.set(state, 'modal_call_open', !state.modal_call_open)
      },
      TOGGLE_MODAL_MENU: (state) => {
        Vue.set(state, 'modal_menu_open', !state.modal_menu_open)
      }
    },
    getters: {
      device (state) {
        return state.screen_width.device
      },
      device_width (state) {
        return state.screen_width.width
      },
      device_height (state) {
        return state.screen_width.height
      },
      env (state) {
        return state.env
      }
    }
  })

  return store
}

export default CreateStore



