<template>
    <scroll-animate :animation="animation" :from="from" :speed="'faster'" class="top-btn">
      <div class="top-btn-style" @click="$scroll.to('#header')">
        <i class="fas fa-arrow-up"></i>
      </div>
    </scroll-animate>
</template>

<script>
  export default {
    data: () => ({
      animation: 'slideIn',
      from: 600
    }),
    watch: {
      '$scroll.y' (n) {
        if (this.from < n) {
          this.animation = 'slideInUp'
        } else {
          this.animation = 'hide'
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";
  .top-btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    transition: .5s all;
    z-index: 2;
    cursor: pointer;
    .top-btn-style {
      border-radius: 0.25rem;
      transition: .5s all;
      cursor: pointer;
      opacity: 0.4;
      padding: 20px;
      background: white;
      border: 2px solid $primary_color;
      i {
        font-size: 1.4em;
      }
      &:hover {
        opacity: 1;
      }
    }
  }
  .hide {
    &.top-btn {
      bottom: -200px;
    }
  }
</style>



