<template>
  <footer>
    <b-container>
      <div class="footer-flex">
        <div class="logo_wrap">
          <!--<div class="heartbeat">-->
            <!--<i class="mdi mdi-heart"></i>-->
          <!--</div>-->
          <div class="footer_logo">
            <img src="../../static/img/logo.jpg" alt="logo">
          </div>
          <span>CORDIS</span>
          <span class="slogan">
            <template v-if="$DEVICE > 2">-</template> на страже вашего здоровья 24/7.<br v-if="$DEVICE === 1"> Гарантия анонимности
                  </span>
        </div>
        <div class="copyright">
          2017 - {{ new Date().getFullYear() }} ® Cordis.com.ua
        </div>
      </div>
    </b-container>
  </footer>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";
  footer {
    height: 70px;
    background: $accent_color;
    color: #D8D8D7;
    font-size: 2em;
    display: flex !important;
    align-items: center;
    .footer-flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .copyright {
        font-size: 0.5em;
      }
    }
    .logo_wrap {
      display: flex;
      align-items: center;
      .footer_logo {
        height: 70px;
        width: 70px;
        display: inline-block;
      }
      .slogan {
        display: inherit;
        position: inherit;
        bottom: unset;
      }
    }
  }

  @media (max-width: 992px) {
    footer {
      height: inherit;
      text-align: center;
      .footer-flex {
        display: block;
      }
      div {
        padding: 5px 0;
      }
    }
  }
</style>



