// Animate.css
import 'animate.css/animate.css'

export default {
  data: () => ({
    done: true,
    active: true,
    y: 0,
    height: 0,
    classNames: {
      animated: true
    },
    timeouted: true
  }),
  props: {
    timeout: {
      type: Number,
      default: 0
    },
    from: {
      type: [Number, Boolean],
      default: true
    },
    to: {
      type: Number,
      default: 0
    },
    offset: {
      type: Number,
      default: 0
    },
    animation: {
      type: String,
      default: ''
    },
    speed: {
      type: String,
      default: ''
    }
  },
  methods: {
    activate () {
      this.timeouted = true
      setTimeout(() => {
        const obj = {}
        obj[this.animation] = true
        obj[this.speed] = true
        this.$set(this, 'classNames', {
          animated: true,
          ...obj
        })
        this.done = true
      }, this.timeout)
    },
    setY () {
      this.y = this.$refs.scroll.getBoundingClientRect().top + window.scrollY
      this.height = this.$refs.scroll.getBoundingClientRect().height
    }
  },
  watch: {
    '$scroll.y' (n) {
      if (!this.timeouted) {
        if (typeof this.from === 'number' && n >= this.from && (n < this.to || this.to === 0)) {
          this.activate()
        } else if (typeof this.from === 'boolean' && this.from && n >= this.y - this.offset && n <= this.y + this.height) {
          this.activate()
        }
      }
    },
    '$mq.resize' () {
      this.setY()
    },
    'animation' () {
      this.timeouted = false
      this.activate()
    }
  },
  mounted () {
    this.setY()
  },
  template: `
    <div ref="scroll" :class="classNames" :style="{'visibility': done ? 'visible' : 'hidden'}">
      <slot></slot>
    </div>
  `
}



