<template>
  <div id="header">
    <nav class="navbar navbar-expand fixed-top wz-top-header p-0" :ref="'header'"
         :class="{'scroll-active' : resizeHeader && $DEVICE >= 3}">
      <div class="container container-fluid" style="height: 100%;">
        <div class="wz-navbar-header">
          <div class="wz-navbar-header__logo" :class="{'mobile_page_name': $DEVICE <= 3}"
               @click="$scroll.to('#header')">
            <img src="../../static/img/logo.jpg" alt="logo">
            <h1 class="header_title" style="font-size: 32px;">CORDIS</h1>
            <span v-if="$DEVICE > 3" class="slogan"> - на страже Вашего здоровья 24/7. Гарантия анонимности</span>
          </div>
        </div>
        <div style="cursor: pointer" v-call class="wz-right-navbar" v-if="$DEVICE > 3">
          <div>+380 (67) 103-8-103</div>
          <div style="font-size: 0.7em;">Бесплатная консультация</div>
        </div>
        <div v-else class="header-menu" @click="$store.commit('TOGGLE_MODAL_MENU')">
          <i class="fas fa-bars"></i>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
  export default {
    data: () => ({
      edge: 400,
      resizeHeader: false
    }),
    watch: {
      '$scroll.y' (n) {
        this.edge < n ? this.resizeHeader = true : this.resizeHeader = false
      }
    }
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";

  .navbar-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    li {
      padding: 0 10px;
      cursor: pointer;
      i {
        font-size: 1.5rem;
        &:hover {
          color: #dc3545
        }
      }
    }
  }

  .nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    list-style: none;
  }

  .wz-top-header {
    background-image: linear-gradient(270deg, #fff 50%, #000 50%);
    background-color: #000;
    z-index: 3 !important;
    border: 0;
    margin-bottom: 0;
    padding: 0;
    height: $header_height;
    transition: all .2s;
    &.scroll-active {
      height: 60px;
    }
    .wz-navbar-header {
      color: white;
      display: flex;
      width: 100%;
      height: 100%;
      align-items: center;
      /*background-image: linear-gradient(291deg, #fff 7%, #D4AF37 5%, #000);*/
      background-image: linear-gradient(291deg, #fff 7%, #000 7%);
      .wz-navbar-header__logo {
        line-height: 60px;
        height: 60px;
        font-size: 32px;
        font-weight: bold;
        padding: 0 20px;
        margin: 0;
        display: flex;
        align-items: center;
        width: 100%;
        white-space: pre-wrap
      }
      .wz-navbar-header__search {
        display: inline-block;
        margin: auto;
        width: 100%;
      }
      .for-button {
        align-items: center;
        display: flex;
        button {
          margin-left: 10px !important;
        }
      }
    }
    .page-title {
      font-size: 1.462rem;
      line-height: 60px;
      float: left;
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
      padding: 0 20px;
      .icon {
        font-size: 1.7rem;
        margin-right: 10px;
      }
    }
    .wz-right-navbar {
      min-width: 270px;
      font-size: 28px;
      font-weight: bold;
      display: flex;
      align-items: center;
      height: 100%;
      white-space: nowrap;
      flex-direction: column;
      justify-content: center;
      /*color: #fff*/
    }
    &.searching {
      .wz-navbar-header {
        width: 100%;
      }
      .wz-right-navbar {
        width: 0;
      }
    }
    .header-menu {
      position: absolute;
      right: 0;
      padding: 20px;
      color: white;
      font-size: 1.4em;
    }
  }

  @media (max-width: 1199px) {
    .wz-top-header .wz-navbar-header .wz-navbar-header__logo {
      font-size: 26px;
    }
  }

  @media (max-width: 992px) {
    .wz-top-header {
      .header-menu {
        color: #212529;
      }
    }
  }

  @media (max-width: 768px) {
    .wz-top-header {
      .header-menu {
        color: white;
      }
    }
    .wz-top-header {
      height: 50px;
      background: $accent_color;
    }
    .wz-top-header {
      .wz-navbar-header {
        min-width: 100%;
        background-image: unset;
        .wz-navbar-header__logo {
          width: 100%;
          height: 50px;
          padding: 0 10px;
          line-height: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
          h1 {
            display: flex;
            align-items: center;
            margin-bottom: 0;
            justify-content: center;
            font-size: 1.2rem;
            text-align: center;
            small {
              color: #dc3545;
              display: block;
            }
            span {
              flex: none;
            }
            i {
              margin-right: 10px !important;
              flex: none;
            }
          }
          i {
            flex: 1;
            margin-right: 10px;
          }
          span {
            flex: 5;
            text-align: center;
            font-size: 1.2rem;
            i {
              margin-right: 3px;
              font-size: 24px;
            }
          }
        }
      }
    }
  }

  .wz-navbar-header__logo {
    &:hover {
      .heartbeat {
        animation: heartbeatt 1s infinite;
      }
    }
  }

  .heartbeat {
    display: inline-block;
    /*color: #f9a433;*/
    color: #ffc107;
  }

  .slogan {
    display: inline-block;
    font-size: 0.6em;
    position: relative;
    bottom: 4px;
  }

  @keyframes heartbeatt {
    0% {
      transform: scale(.75);
    }
    20% {
      transform: scale(1);
    }
    40% {
      transform: scale(.75);
    }
    60% {
      transform: scale(1);
    }
    80% {
      transform: scale(.75);
    }
    100% {
      transform: scale(.75);
    }
  }
</style>



