<template>
  <section style="position: relative; top: 50px; background: white;padding-bottom: 60px" class="white-section" id="onas">
    <scroll-animate :animation="'fadeIn'" :offset="400" class="container">
      <h2>О нас</h2>
      <b-row>
        <b-col lg="8" md="12">
          <div class="text-o-nas">
            <b>Медицинская команда CORDIS</b> - специализируется на оказании <b>круглосуточной медицинской помощи</b>.
            Мы занимаемся <b>транспортировкой больных любой степени тяжести</b>, начиная от банальной транспортировки,
            вплоть до сопровождения тяжелобольных от стационара до стационара в любую часть Украины.
            А так же помогаем людям страдающим вследствии алкогольной или наркотической интоксикации организма<br><br>
            Мы помогаем людям побороть их плохое самочувствие после употребления алкоголя <b>на дому и в стационаре</b>.
            Медицинский работник приедет к Вам в кратчайшие сроки <b>в любое время суток и в любой день недели</b>,
            удобные для Вас. Все наши сотрудники оснащены медицинской сумкой с необходимым набором медикаментов
            для оказания неотложной помощи на случай аллергической реакции, одноразовыми расходными материалами,
            штативом, глюкометром, тонометром, пульсоксиметром и электрокардиографом для постоянного контроля
            состояния пациента. Так же мы располагаем специальным медицинским <b>транспортом категории С</b>,
            которое дает нам возможность транспортировать с комфортом больных в сопровождении медицинской бригады и
            родственников.
          </div>
        </b-col>
        <b-col lg="4" md="12" id="time">
          <scroll-animate :animation="'zoomIn'" :offset="400" class="time">
            <h3><span>Время работы</span></h3>
            <div style="color: #dc3545;" class="time-text">Круглосуточно</div>
            <div style="margin: 10px 0;" class="time-text">Без праздников</div>
            <div class="time-text">Без выходных</div>
            <div class="time-text-usl">
              C 00:00 - 07:00 цена услуг оговаривается индивидуально
            </div>
            <b-button v-call size="lg" variant="warning">
              Позвонить
            </b-button>
          </scroll-animate>
        </b-col>
      </b-row>
    </scroll-animate>
  </section>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  .text-o-nas {
    font-size: 1.2em;
    line-height: 1.7em;
    text-align: justify;
    margin-right: 20px;
    b {
      color: #0087cb;
    }
  }

  .time {
    text-align: center;
    border: 2px solid #2185c5;
    border-radius: 0.25rem;
    .time-text {
      font-size: 1.5em;
      color: #615f5f;
      font-family: "DAYS"
    }
    .time-text-usl {
      font-size: 0.8em;
      color: #615f5f;
      margin-top: 30px;
      margin-bottom: 20px;
    }
    h3 {
      padding: 15px;
      font-family: "Days";
      color: white;
      margin-bottom: 30px;
      background-image: linear-gradient(291deg, #2185c5 13%, #77c4f2 11%, #2185c5);
    }
    button {
      margin-bottom: 20px;
    }
  }

  @media (max-width: 992px) {
    .time {
      margin-top: 50px;
    }
  }

  @media (max-width: 544px) {
    .text-o-nas {
      margin: 0;
      text-align: left;
    }
  }
</style>



