export default {
  install (Vue, store) {
    Vue.mixin({
      computed: {
        $DEVICE () {
          return store.getters.device
        },
        $DEVICE_WIDTH () {
          return store.getters.device_width
        },
        $APP () {
          return store.state.app
        },
        $APP_ENV () {
          return store.getters.env
        }
      }
    })
  }
}



