<template>
  <div id="app">
    <app-header></app-header>
    <banner></banner>
    <steps-section></steps-section>
    <dropper-section></dropper-section>
    <odds-section></odds-section>
    <!--<uslugi></uslugi>-->
    <!--<timeonas></timeonas>-->
    <!--<app-details></app-details>-->
    <specialist></specialist>
    <contacts></contacts>
    <app-footer></app-footer>
    <call-modal></call-modal>
    <menu-modal></menu-modal>
    <upp-button></upp-button>
  </div>
</template>

<script>
  import appHeader from './views/Header.vue'
  import banner from './views/Banner.vue'
  import uslugi from './views/Uslugi.vue'
  import timeonas from './views/Time.vue'
  import appDetails from './views/Details.vue'
  import specialist from './views/Specialist.vue'
  import contacts from './views/Contacts.vue'
  import appFooter from './views/Footer.vue'
  import callModal from './components/call-modal.vue'
  import menuModal from './components/menu-modal.vue'
  import uppButton from './components/upp-button.vue'
  import stepsSection from './views/StepsSection'
  import dropperSection from './views/DropperSection'
  import oddsSection from './views/OddsSection'
  export default {
    name: 'CORDIS_APP',
    data: () => ({}),
    components: {
      appHeader,
      banner,
      uslugi,
      timeonas,
      appDetails,
      specialist,
      contacts,
      appFooter,
      callModal,
      menuModal,
      uppButton,
      stepsSection,
      dropperSection,
      oddsSection
    },
    created () {
      this.$store.commit('SET_DEVICE_WIDTH', { data: window.innerWidth, height: window.innerHeight })
    },
    watch: {
      '$mq.resize' () {
        this.$store.commit('SET_DEVICE_WIDTH', { data: window.innerWidth, height: window.innerHeight })
      }
    }
  }
</script>

<style lang="scss">
  * {
    -webkit-overflow-scrolling: touch;
  }

  html {
    font-size: 13px;
  }

  section {
    padding: 10px 0 40px;
  }

  img {
    max-height: 100%;
    max-width: 100%;
  }

  .white-section {
    background: white;
  }

  body {
    background-color: #f5f5f5 !important;
    margin: 0;
    font-family: "Museo Sans Cyrl", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !important;
  }

  h2 {
    width: 100%;
    text-align: center;
    font-family: "Days" !important;
    margin: 40px 0 !important;
    /*font-size: 3em !important;*/
    span {
      background: #0087cb;
      padding: 8px 20px;
      color: white;
      line-height: 1.5;
      border-radius: 0.25rem;
      background-image: linear-gradient(291deg, #2185c5 13%, #77c4f2 11%, #2185c5);
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
    position: absolute;
    height: 100%;
  }

  .fade-leave-active {
    display: none;
  }

  .fade-long-enter-active, .fade-long-leave-active {
    transition: opacity .5s;
  }

  .fade-long-enter, .fade-long-leave-to {
    opacity: 0;
    position: absolute;
    height: 100%;
  }

  .fade-long-leave-active {
    display: none;
  }

  .el-container {
    width: 1140px !important;
    margin: 0 auto !important;
    display: block !important;
  }

  .el-main {
    padding: 20px 20px 20px 0 !important;
  }

  @media (max-width: 768px) {
    * {
      cursor: pointer !important;
    }
    .wz-wrapper {
      padding-top: 50px;
    }
    input[type="color"],
    input[type="date"],
    input[type="datetime"],
    input[type="datetime-local"],
    input[type="email"],
    input[type="month"],
    input[type="number"],
    input[type="password"],
    input[type="search"],
    input[type="tel"],
    input[type="text"],
    input[type="time"],
    input[type="url"],
    input[type="week"],
    select:focus,
    textarea {
      font-size: 16px;
    }
  }

  .message-box {
    max-width: 300px;
    text-align: center;
    margin: 170px auto;
  }

  .message-box-white {
    background-color: #fff;
    padding: 20px
  }
</style>



