<template>
    <div>
      <section style="padding: 1px">
        <scroll-animate :animation="'fadeIn'" :offset="500" id="intox">
          <b-container class="details">
            <b-row>
              <template v-if="$DEVICE > 3">
                <b-col cols="5">
                  <img src="../../static/img/intox.jpg">
                </b-col>
              </template>
              <b-col style="position: relative">
                <h3>
                <span>Снятие алкогольной интоксикации<br> на дому</span>
                </h3>
                <div class="details-text">
                  Капельница от алкоголя на дому цена которой доступна практически каждому,
                  <b>абсолютно безопасна для человека</b>, не может нанести вреда организму.
                  Такая процедура является уместной даже в случае с небольшой интоксикацией.
                  Еще одно ее преимущество – у пациента <b>нет потребности в посещении клиники</b>.
                  Кроме того, можно отметить следующие положительные моменты в установке капельницы от
                  алкоголя на дому: После применения инфузионной терапии у больного <b>налаживается работа всех
                  органов и систем</b>, в особенности желудочно-кишечного тракта и печени. Капельница является
                  действенным способом устранить последствия принятия алкоголя, вернуть человеку психический и физический комфорт;
                  Капельница на дому – это эффективная профилактика абстинентного синдрома и очищение организма.<br><br>

                  <b>Мы поможем Вам</b> предотвратить или побороть данное состояние. После курса внутривенных инфузий
                  <b>Ваше состояние значительно улучшится</b>, восстановится сон и нормализуются функции организма.
                  Не отнимет много времени. За считанные часы пациент возвращается в привычный режим дня, освобождается от
                  похмелья и плохого самочувствия.
                </div>
                <div class="button-place">
                <span>
                  от 900 грн.
                </span>
                  <b-button v-call size="lg" variant="danger">
                    Позвонить
                  </b-button>
                </div>
              </b-col>
            </b-row>
          </b-container>
        </scroll-animate>
      </section>
      <section class="white-section">
        <scroll-animate :animation="'fadeIn'" :offset="500" id="car">
          <b-container class="details">
            <b-row>
              <b-col style="position: relative; min-height: 570px">
                <h3 class="usual">
                  Транспортировка больных <br>
                  Перевозка пациентов
                </h3>
                <div class="details-text">
                  Сильные травмы, нетрудоспособность, старость - несколько причин, при которых требуется такая услуга,
                  как перевозка больного. Такие люди, которым приходилось сталкиваться с подобными проблемами знают,
                  что осуществление такой задачи может оказаться невозможным в случае, если нет необходимых условий
                  перевозки. Даже имея большую и просторную машину, самостоятельно решить задачу просто не удается.
                  Близкого вам человека нужно не просто перевезти, но и обеспечить комфорт и безопасность его состояния,
                  а иногда и жизни. <br><br>

                  <b>Наши реанимобили категории С оснащены профессиональным техническим оборудованием</b>,
                  которое обеспечивает комфортабельные условия транспортировки вне зависимости от расстояния поездки.
                  В нашем обеспечении так же <b>имеются носилки всех видов</b> (лежачие, сидячие, мягкие, транспортировочный щит),
                  что позволяет спустить или поднять больного на любой этаж вне зависимости от ширины прохода.
                </div>
                <div class="button-place">
              <span>
                от 650 грн.
              </span>
                  <b-button v-call size="lg" variant="outline-danger">
                    Позвонить
                  </b-button>
                </div>
              </b-col>
              <template v-if="$DEVICE > 3">
                <b-col cols="5">
                  <img class="usual" src="../../static/img/car.png">
                </b-col>
              </template>
            </b-row>
          </b-container>
        </scroll-animate>
      </section>
      <section>
        <scroll-animate :animation="'fadeIn'" :offset="500" id="detox">
          <b-container class="details">
            <b-row>
              <template v-if="$DEVICE > 3">
                <b-col cols="5">
                  <img class="usual" src="../../static/img/detox.jpg">
                </b-col>
              </template>
              <b-col style="position: relative; min-height: 550px">
                <h3 class="usual">
                  Детокс Капельницы при отравлении
                </h3>
                <div class="details-text">
                  При отравлении некачественной пищей признаки интоксикации появляются через 15 мин - 6 часов.
                  Пищевая интоксикация пагубно влияет на организм. Патоген выделяет токсины, попадая в просвет кишечника.
                  Это могут быть вирусы или бактерии, которые, размножаясь, атакуют органы и системы человека.
                  Всасывание воды и питательных веществ нарушается, <b>обезвоживание организма приводит к общему истощению</b>.
                  За несколько дней человек может потерять несколько литров воды, с которой больного покидают
                  такие полезные вещества как кальций, натрий, хлор, калий. Основное преимущество капельницы при
                  пищевом отравлении перед другими способами избавления от интоксикации - <b>это быстрое поступление
                  препаратов через вену в кровь</b>. Если действие таблеток начинается через 30 минут в среднем,
                  то <b>действие капельницы пациент чувствует практически сразу</b>. С помощью быстрого введения лекарственных
                  средств через кровь удается спасти жизни людей в тех случаях, когда отсчет времени идет на минуты.
                </div>
                <div class="button-place">
                <span>
                  от 800 грн.
                </span>
                  <b-button v-call size="lg" variant="danger">
                    Позвонить
                  </b-button>
                </div>
              </b-col>
            </b-row>
          </b-container>
        </scroll-animate>
      </section>
    </div>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  .details {
    margin-top: 100px;
    margin-bottom: 100px;
    b {
      text-decoration: underline;
      color: #dc3545
    }
    h3 {
      text-align: center;
      border-radius: 0.25rem;
      font-family: "Days";
      padding: 10px;
      &:not(.usual) {
        color: white;
        background-image: linear-gradient(291deg, #dc3545 11%, #2185c5);
      }
    }
    .details-text {
      margin-top: 20px;
      font-size: 1.2em;
      line-height: 1.5em;
    }
    img {
      border-radius: 0.25rem;
      transition: .5s all;
      &:not(.usual) {
        filter: blur(2px);
      }
      &:hover {
        filter: blur(0px);
      }
    }
    .button-place {
      position: absolute;
      bottom: 0;
      margin-top: 20px;
      width: calc(100% - 30px);
      button {
        font-size: 2em;
        width: 100%
      }
      span {
        text-align: center;
        font-size: 1.4em;
        padding: 10px 0 30px;
        color: #615f5f;
        width: 100%;
        display: block;
      }
    }
  }

  @media (max-width: 1199px) {
    .details {
      .button-place {
        position: static;
        width: 100%;
      }
    }
  }

  @media (max-width: 544px) {
    .details {
      margin: 30px 0;
    }
  }
</style>



