/* eslint-disable */

function gtag_report_conversion() {
  gtag('event', 'conversion', {
    'send_to': 'AW-777278796/EWuICMCY5pMBEMyq0fIC'
  });
  return false;
}

export default {
  install (Vue, store) {
    Vue.directive('call', {
      bind: function (el, bindings, vnode) {
        if (store.state.screen_width.device < 3) {
          el.innerHTML = `<a style="color:inherit; text-decoration: none" href="tel:${store.state.phone}">${el.innerHTML}</a>`
        } else {
          el.addEventListener('click', installclick)
        }
        el.addEventListener('click', gtag_report_conversion)
      },
      unbind: function (el) {
        el.removeEventListener('click', installclick)
        el.removeEventListener('click', gtag_report_conversion)
      }
    })

    function installclick () {
      store.commit('TOGGLE_MODAL_CALL')
    }
  }
}



