/* eslint-disable */
import Vue from 'vue'
import App from './App'
import CreateStore from './store/index'
import router from './router.js'
import customPlugin from './plugins/custom'
import VueTypedJs from 'vue-typed-js'

// Bootstrap
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import '@mdi/font/css/materialdesignicons.css'
import '@fortawesome/fontawesome-free/css/all.css'

// Museo font
import '../static/fonts/museo.css'
import '../static/fonts/junegull.css'

// Media query helper
import vMediaQuery from 'v-media-query'

// Scroll plugin
import vusc from './plugins/vue-scroll-controller'

// Media call plugin
import mediaCall from './plugins/media-call'

// Vue-analitcs
import VueAnalytics from 'vue-analytics'


Vue.config.productionTip = false
Vue.config.devtools = true

Vue.use(BootstrapVue)
Vue.use(vMediaQuery)
Vue.use(VueTypedJs)
Vue.use(VueAnalytics, {
  id: 'UA-129742168-1',
  router,
  disableScriptLoader: true
})

const store = CreateStore()

Vue.use(customPlugin, store)
Vue.use(mediaCall, store)
Vue.use(vusc, store, {
  offset: -100,
  duration: 500
})
Vue.prototype.$eb = new Vue()

function createApp () {
  const app = new Vue({
    router,
    store,
    components: { App },
    template: '<App/>'
  })

  app.$mount('#app')
}

export default createApp



