// Scroll controler
import { scroll as instance } from './scroll'
import scrollStore from './scroll-store'
import scrollAnimate from './scroll-animate'

export default {
  install (Vue, store, options = {}) {
    const Scroll = instance(options)
    Vue.prototype.$scroll = new Vue({ ...Scroll, store })
    store.registerModule('vusc', scrollStore)
    Vue.component('scroll-animate', scrollAnimate)
  }
}



