<template>
  <div class="special-banner">
    <scroll-animate :animation="'pulse'" :offset="offset" :timeout="300" class="special-text">
      <h2>У Вас остались вопросы?</h2>
      <b-button v-call size="lg" variant="warning">
        Свяжитесь с нами
      </b-button>
    </scroll-animate>
  </div>
</template>

<script>
  export default {
    data: () => ({}),
    computed: {
      offset () {
        return this.$DEVICE > 3 ? 300 : 900
      }
    }
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";
  .special-banner {
    position: relative;
    &:before {
      content: '';
      height: 100%;
      width: 100%;
     // background: rgba($primary_color, 0.5);
      position: absolute;
      z-index: 2;
      background-color: $accent_color;
    }
    display: flex;
    justify-content: center;
    align-items: center;
    /*background-size: cover;*/
    height: 333px;
    /*background-attachment: fixed;*/
    /*background-image: url('../../static/img/paralax.jpg');*/
    .special-text {
      position: relative;
      z-index: 2;
      padding: 20px 50px;
      //background-image: linear-gradient(291deg, $primary_color 11%, #000);
      text-align: center;
      color: white;
      //border: 1px solid $primary_color;
      //box-shadow: 0px 10px 100px $primary_color;
      border-radius: 0.25rem;
      h2 {
        font-size: 3em;
      }
      button {
        font-size: 2em;
        background-color: $primary_color;
      }
    }
  }

  @media (max-width: 544px) {
    .special-banner {
      .special-text {
        padding: 20px 10px;
        h2 {
          font-size: 2em !important;
        }
      }
    }
  }
</style>



