<template>
  <section class="white-section" style="margin-top: 0; margin-bottom: 0; padding-bottom: 90px; padding-top: 40px" id="contacts">
    <scroll-animate :animation="'fadeInUp'" :offset="offset" :timeout="500">
      <b-container>
        <h2>Контакты</h2>
        <div class="contacts">
          <div class="contact"><i class="fas fa-phone-square"></i><div style="cursor: pointer" v-call>+380 (67) 103-8-103</div></div>
          <div class="contact"><i class="fas fa-clock"></i><div>Круглосуточно, без выходных</div></div>
          <div class="contact"><i class="fas fa-globe"></i><a href="http://cordis.com.ua">cordis.com.ua</a></div>
          <div class="contact"><i class="fas fa-at"></i><a href="mailto:med.cordis@gmail.com">med.cordis@gmail.com</a></div>
        </div>
      </b-container>
    </scroll-animate>
  </section>
</template>

<script>
  export default {
    data: () => ({}),
    computed: {
      offset () {
        return this.$DEVICE > 3 ? 550 : 900
      }
    }
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";
  .contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 380px;
    margin: 0 auto;
    .contact {
      padding: 5px;
      display: flex;
      justify-content: space-between;
      font-size: 1.7em;
      width: 380px;
      align-items: center;
      i {
        margin-right: 15px;
        color: $primary_color;
      }
      div,a {
        color: #212529;
        text-decoration: none;
        width: 100%;
      }
    }
  }

  @media (max-width: 544px) {
    .contacts {
      .contact {
        font-size: 1.4em !important;
        width: 320px;
      }
    }
  }
</style>



