import Vue from 'vue'

export default {
  namespaced: true,
  state: {
    scroll_position_y: 0,
    scroll_position_x: 0
  },
  actions: {},
  mutations: {
    SET_SCROLL_POSITION: (state, { x, y }) => {
      Vue.set(state, 'scroll_position_y', y)
      Vue.set(state, 'scroll_position_x', x)
    }
  },
  getters: {
    y: (state) => {
      return state.scroll_position_y
    }
  }
}



