import scrollTo from 'scroll-to-element'

const duration = 1500
const ease = 'out-circ'

function __scroll (to, options, promise) {
  scrollTo(to, options)
  return Promise.resolve(new Promise(resolve => {
    setTimeout(() => {
      resolve(promise)
    })
  }))
}

export const scroll = function (options) {
  return {
    data: () => ({
      x: 0,
      y: 0
    }),
    options: {
      offset: options.offset || 0,
      duration: options.duration || duration,
      ease: options.ease || ease
    },
    created () {
      // TODO: destroy event on vue destroy hook
      window.addEventListener('scroll', this.__setScrollToStore)
    },
    methods: {
      __setScrollToStore () {
        this.$set(this, 'x', window.pageXOffset)
        this.$set(this, 'y', window.pageYOffset)
        this.$store.commit('vusc/SET_SCROLL_POSITION', {
          x: window.pageXOffset,
          y: window.pageYOffset
        })
      },

      to (to, { options = {}, promise } = {}) {
        const Options = { ...this.$options.options, ...options }
        __scroll(to, Options, promise)
      }
    }
  }
}



