<template>
  <section style="position: relative; top: 50px;" class="container" id="uslugi">
    <h2><span>Наши услуги</span></h2>
    <div class="uslugi">
        <scroll-animate :animation="'fadeInUp'" :offset="550" :speed="'faster'" class="uslugi-items">
          <div>
            <i class="fa fa-wine-glass-alt"></i>
            <i class="fa fa-notes-medical"></i>
          </div>
          <h3 class="title">
            Снятие алкогольной интоксикации на дому
          </h3>
          <div class="text">
            Наши мобильные бригады приедут к Вам в кратчайшие сроки и помогут Вам побороть плохое самочувствие. <br> Мы даем 100% гарантию качества оказанных нами услуг.
          </div>
          <div class="button-side">
            <b-button size="md" variant="primary" @click="$scroll.to('#intox')">
              Подробнее
            </b-button>
          </div>
        </scroll-animate>
        <scroll-animate :animation="'fadeInUp'" :speed="'fast'" :offset="550" class="uslugi-items">
          <div>
            <i class="fa fa-ambulance"></i>
            <i class="fa fa-hospital"></i>
          </div>
          <h3 class="title">
            Транспортировка больных
          </h3>
          <div class="text">
            Мы - команда, оказывающая услуги по перевозке лежачих больных, транспортировке больных, сопровождению больных по Одессе и Одесской области, а также по всей территории Украины на специализированном медицинском транспорте.
          </div>
          <div class="button-side">
            <b-button size="md" variant="primary" @click="$scroll.to('#car')">
              Подробнее
            </b-button>
          </div>
        </scroll-animate>
        <scroll-animate :animation="'fadeInUp'" :offset="550" class="uslugi-items">
          <div>
            <i class="fa fa-tint"></i>
            <i class="fa fa-notes-medical"></i>
          </div>
          <h3 class="title">
            DETOX капельницы при отравлениях
          </h3>
          <div class="text">
            Что делать при отравлении?Отравились испорченной или некачественной пищей?
            Детокс-капельница используется для быстрой помощи при отравлении различными агентами.
            Быстро выводит токсины и ставит Вас на ноги в кратчайшие сроки.</div>
          <div class="button-side">
            <b-button size="md" variant="primary" @click="$scroll.to('#detox')">
              Подробнее
            </b-button>
          </div>
        </scroll-animate>
      </div>
  </section>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  .uslugi {
    display: flex;
    justify-content: space-around;
    .uslugi-items {
      width: 33.33%;
      position: relative;
      padding: 20px 5px 70px;
      margin: 15px;
      border-radius: 0.25rem;
      border: 2px solid #eee;
      background: white;
      text-align: center;
      margin-top: 20px;
      i {
        font-size: 4em;
        color: #5cc4f6
      }
      .title {
        font-size: 1.3em;
        font-family: "days";
        color: #575a5d;
        padding: 10px;
        margin-top: 10px;
        min-height: 80px;
      }
      .text {
        text-align: left;
        padding: 10px;
      }
      .button-side {
        position: absolute;
        bottom: 20px;
        left: 0;
        width: 100%;
      }
    }
  }

  @media (max-width: 992px) {
    .uslugi {
      flex-direction: column;
      .uslugi-items {
        width: 100%;
        margin: 0;
        margin-bottom: 30px;
        padding-bottom: 20px;
        .title {
          min-height: 0px;
          font-size: 1.9em;
        }
        .text {
          font-size: 1.4em;
          text-align: center;
        }
        .button-side {
          margin-top: 20px;
          margin-bottom: 10px;
          position: static;
          button {
            font-size: 1.5em;
          }
        }
      }
    }
  }

  @media (max-width: 544px) {
    .uslugi {
      flex-direction: column;
      .uslugi-items {
        .text {
          font-size: 1.3em;
          text-align: left;
        }
      }
    }
  }
</style>



